<template>
  <div class="zp">
    <img src="../assets/bg.png" class="bg" :style="runstatus?'transform:rotate('+m+'deg)':'transition:none'">
    <img src="../assets/zz.png" class="zz" @click="run()">
  </div>
</template>
<script>
export default {
  name: "zp",
  data() {
    return {
      runstatus: false,
      m: 0
    };
  },
  methods: {
    run() {
      let m = Math.ceil(Math.random() * 10000);
      this.m = m;
      this.runstatus = !this.runstatus;
    }
  }
};
</script>
<style scoped lang="scss">
.zp {
  text-align: center;
}
.bg {
  width: 7.5rem;
  height: 7.5rem;
  transition: transform 2s linear;
}
.zz {
  width: 7.5rem;
  height: auto;
  position: relative;
  top: -7.55rem;
  z-index: 10;
}
</style>


